<template>
  <div class="main_cont">
    <!-- BootstrapVue Done -->
    <base-header class="pb-6 pb-8 pt-5 pt-md-8 ">
      <!-- Card stats -->
    </base-header>

    <b-container fluid class="mt--7">
      <form @submit.prevent="createOz()">
        <b-row class="justify-content-center">
          <b-col lg="8">
            <b-card class="p-5" no-body>
              <b-tabs>
                <b-tab no-body title="O'zbekcha">

                  <div class="form_mains">
                    <div class="inputs ">
                      <div>
                        <label>Title</label>
                      </div>
                      <div>
                        <input type="text" v-model="title" class="form-control">
                      </div>
                    </div>
                    <div class="inputs ">
                      <div>
                        <label>Short description</label>
                      </div>
                      <div>
                        <input type="text" v-model="short_description" class="form-control">
                      </div>
                    </div>
                    <div class="inputs inputs_quil">
                      <div>
                        <label>Description</label>
                      </div>
                      <quill-editor
                        :required="true"
                        ref="myQuillEditorOrg"
                        v-model="description"
                      />
                    </div>


                  </div>
                </b-tab>

                <b-tab no-body title="Rus">
                  <div class="inputs">
                    <div>
                      <label>Title</label>
                    </div>
                    <div>
                      <input type="text" v-model="titleru" class="form-control">
                    </div>
                  </div>
                  <div class="inputs ">
                    <div>
                      <label>Short description</label>
                    </div>
                    <div>
                      <input type="text" v-model="short_descriptionru" class="form-control">
                    </div>
                  </div>
                  <div class="inputs inputs_quil">
                    <div>
                      <label>Description</label>
                    </div>
                    <quill-editor
                      :required="true"
                      ref="myQuillEditorOrg"
                      v-model="descriptionru"
                    />
                  </div>


                </b-tab>

                <b-tab no-body title="Qoraqalpoq">

                  <div class="inputs">
                    <div>
                      <label>Title</label>
                    </div>
                    <div>
                      <input type="text" v-model="titleen" class="form-control">
                    </div>
                  </div>
                  <div class="inputs ">
                    <div>
                      <label>Short description</label>
                    </div>
                    <div>
                      <input type="text" v-model="short_descriptionen" class="form-control">
                    </div>
                  </div>
                  <div class="inputs inputs_quil">
                    <div>
                      <label>Description</label>
                    </div>
                    <quill-editor
                      :required="true"
                      ref="myQuillEditorOrg"
                      v-model="descriptionen"
                    />
                  </div>

                </b-tab>
              </b-tabs>
            </b-card>
          </b-col>
          <b-col lg="4">
            <b-card class="p-5" no-body>
              <div class="inputs">
                <div>
                  <label>Order</label>
                </div>
                <div>
                  <input type="text" v-model="order" class="form-control">
                </div>
              </div>
              <div class="inputs">
                <div>
                  <label>Link</label>
                </div>
                <div>
                  <input type="text" v-model="link" class="form-control">
                </div>
              </div>

              <div class="inputs">
                <b-form-select v-model="selected" :options="options" multiple :select-size="4"></b-form-select>
              </div>

              <div class="create_form mt-4">
                <img :src="imageUrl[0]" width="100%" height="150px"   alt="">
                <label class="btn btn-primary my-3" for="cr_image1">Card rasm</label>
                <input type="file" class="form-control d-none"
                       id="cr_image1"
                       @change="trySubmitFile"
                       accept="*"
                       name="datafiles"
                       ref="datafiles"

                >
              </div>

              <div class="create_form">
                <img :src="imageUrl1[0]" width="100%" height="150px"   alt="">
                <label class="btn btn-primary my-3" for="cr_image2">Ichki rasm</label>
                <input type="file" class="form-control d-none"
                       id="cr_image2"
                       @change="trySubmitFile1"
                       accept="*"
                       name="datafiles"
                       ref="datafiles"
                >
              </div>

              <div class="create_form">
                <img :src="imageUrl2[0]" width="100%" height="150px"   alt="">
                <label class="btn btn-primary my-3" for="cr_image3">Asosiy rasm</label>
                <input type="file" class="form-control d-none"

                       id="cr_image3"
                       @change="trySubmitFile2"
                       accept="*"
                       name="datafiles"
                       ref="datafiles"
                >
              </div>
              <div class="inputs">
                <input type="checkbox" @input="is_main=!is_main" v-model="is_main">
                <label class="mx-3">Asosiy</label>
              </div>
              <div class="inputs">
                <input type="checkbox"  @input="is_world=!is_world" v-model="is_world">
                <label class="mx-3">Xalqaro OTM</label>
              </div>
              <div class="inputs">
                <input type="checkbox" @input="is_europe=!is_europe" v-model="is_europe">
                <label class="mx-3">Texnikum</label>
              </div>
              <div class="inputs">
                <input type="checkbox" @input="is_country=!is_country" v-model="is_country">
                <label class="mx-3">O'zbekiston OTMsi</label>
              </div>
              <div class="inputs">
                <b-form-select v-model="selected1" :options="options1" size="sm" class="mt-3"></b-form-select>
              </div>

              <div class="inputs">
                <div class="text-center" >
                  <button class="btn btn-primary" >O'zgartirish</button>
                </div>
              </div>

            </b-card>
          </b-col>
        </b-row>
      </form>
    </b-container>
  </div>
</template>
<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config/index.json";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      selected: [], // Array reference
      options: [
      ],
      selected1: [], // Array reference
      options1: [
      ],
      menus:[],
      link:'',
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      titleru:'',
      images:'',
      titleen:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      cover:'',
      contact:[],
      imageUrl:[],
      myImages:[],
      files_name:[],
      imageUrl1:[],
      myImages1:[],
      files_name1:[],
      imageUrl2:[],
      myImages2:[],
      files_name2:[],
      description:'',
      descriptionru:'',
      descriptionen:'',
      short_description:'',
      short_descriptionru:'',
      short_descriptionen:'',
      is_country:false,
      is_europe:false,
      is_world:false,
      is_main:false,
      inside_image:''
    };
  },
  methods: {
    trySubmitFile(e) {
      this.imageUrl=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      this.imageUrl1=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile2(e) {
      this.imageUrl2=[]
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name2.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages2.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl2.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },

    createOz(){
      const form = new FormData();
      if(this.is_europe===true){
        for (var i=0;i<this.selected.length;i++){
          form.append(`common_programs`, this.selected[i]);
        }
        form.append("order", this.order);
        form.append("title", this.title);
        form.append("country", this.selected1);
        form.append("is_country", this.is_country);
        form.append("is_prof_education", this.is_europe);
        form.append("is_main", this.is_main);
        form.append("is_world", this.is_world);
        form.append("country", this.selected1);
      }
      else{

        if(this.myImages.length==0){
          for (var i=0;i<this.selected.length;i++){
            form.append(`common_programs`, this.selected[i]);
          }
          form.append("order", this.order);
          form.append("link", this.link);
          form.append("description", this.description);
          form.append("short_description", this.short_description);
          form.append("title", this.title);

          form.append("country", this.selected1);
          form.append("is_country", this.is_country);
          form.append("is_europe", this.is_europe);
          form.append("is_main", this.is_main);
          form.append("is_world", this.is_world);
        }
        if(this.myImages.length==1){
          for (var i=0;i<this.selected.length;i++){
            form.append(`common_programs`, this.selected[i]);
          }
          form.append("cover", this.myImages[0]);
          form.append("order", this.order);
          form.append("link", this.link);
          form.append("description", this.description);
          form.append("short_description", this.short_description);
          form.append("title", this.title);

          form.append("country", this.selected1);
          form.append("is_country", this.is_country);
          form.append("is_europe", this.is_europe);
          form.append("is_main", this.is_main);
          form.append("is_world", this.is_world);
        }
        if(this.myImages1.length==1){
          for (var i=0;i<this.selected.length;i++){
            form.append(`common_programs`, this.selected[i]);
          }
          form.append("inside_image", this.myImages1[0]);
          form.append("order", this.order);
          form.append("link", this.link);
          form.append("description", this.description);
          form.append("short_description", this.short_description);
          form.append("title", this.title);

          form.append("country", this.selected1);
          form.append("is_country", this.is_country);
          form.append("is_europe", this.is_europe);
          form.append("is_main", this.is_main);
          form.append("is_world", this.is_world);
        }
        if(this.myImages2.length==1){
          for (var i=0;i<this.selected.length;i++){
            form.append(`common_programs`, this.selected[i]);
          }
          form.append("image", this.myImages2[0]);
          form.append("order", this.order);
          form.append("link", this.link);
          form.append("description", this.description);
          form.append("short_description", this.short_description);
          form.append("title", this.title);

          form.append("country", this.selected1);
          form.append("is_country", this.is_country);
          form.append("is_europe", this.is_europe);
          form.append("is_main", this.is_main);
          form.append("is_world", this.is_world);
        }
        else {
          for (var i=0;i<this.selected.length;i++){
            form.append(`common_programs`, this.selected[i]);
          }
          form.append("order", this.order);
          form.append("link", this.link);
          form.append("description", this.description);
          form.append("short_description", this.short_description);
          form.append("title", this.title);

          form.append("country", this.selected1);
          form.append("is_country", this.is_country);
          form.append("is_europe", this.is_europe);
          form.append("is_main", this.is_main);
          form.append("is_world", this.is_world);
        }
      }
      axios.put(`${baseUrl}universities/${this.$route.params.id}/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{
          this.$toast("Ma'lumotlar o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru
          ;
          for (var i=0;i<this.selected.length;i++){
            form.append(`common_programs`, this.selected[i]);
          }
          form.append("order", this.order);
          form.append("link", this.link);
          form.append("description", this.descriptionru);
          form.append("short_description", this.short_descriptionru);
          form.append("title", this.titleru);
          form.append("country", this.selected1);
          axios.put(`${baseUrl}universities/${res.data.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{
              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              for (var i=0;i<this.selected.length;i++){
                form.append(`common_programs`, this.selected[i]);
              }
              form.append("order", this.order);
              form.append("description", this.descriptionen);
              form.append("short_description", this.short_descriptionen);
              form.append("title", this.titleen);
              form.append("country", this.selected1);
              axios.put(`${baseUrl}universities/${res.data.id}/`,form,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{
                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
        })
    },

  },

  mounted() {
    axios.get(`${baseUrl}universities/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'oz'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.title=this.menus.title
        this.description=this.menus.description
        this.order=this.menus.order
        this.url=this.menus.url
        this.isFooter=this.menus.is_footer
        this.short_description=this.menus.short_description
        this.is_world=this.menus.is_world
        this.is_main=this.menus.is_main
        this.is_europe=this.menus.is_prof_education
        this.is_country=this.menus.is_country
        this.selected1=this.menus.country
        this.link=this.menus.link
        this.imageUrl.push(this.menus.cover)
        this.imageUrl1.push(this.menus.inside_image)
        this.imageUrl2.push(this.menus.image)
        console.log(this.imageUrl,"test")
        this.menus.common_programs.forEach(el=>{
          this.selected.push(el.id)
        })

        console.log(this.selected,"test")
      })
    axios.get(`${baseUrl}universities/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'ru'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.descriptionru=this.menus.description
        this.short_descriptionru=this.menus.short_description
        this.titleru=this.menus.title
        console.log(res)
      })
    axios.get(`${baseUrl}universities/${this.$route.params.id}`,{
      headers:{
        'Accept-Language':'kar'
      }
    })
      .then(res=>{
        this.menus=res.data
        this.descriptionen=this.menus.description
        this.short_descriptionen=this.menus.short_description
        this.titleen=this.menus.title

        console.log(res)
      })
    axios.get(`${baseUrl}university-programs/`)
      .then(res=>{
        res.data.forEach(el=>{
          console.log(el)
          this.options.push({
            value:el.id,
            text: el.title
          })
        })

      })
    axios.get(`${baseUrl}countries/`)
      .then(res=>{
        res.data.forEach(el=>{

          this.options1.push({
            value:el.id,
            text: el.title
          })
        })

      })
  }
}

</script>
